:root {
  --darkGreen: #48644d;
  --orange: #dd7444;
  --lightGreen: #b3c794;
  --titleSize: 20px;
  --normalSize: 17px;
  --subTitleSize: 45px;
}

.ps_wrapper button {
  border: none;
  background-color: transparent;
  position: relative;
}
.ps_wrapper * {
  letter-spacing: 1px;
}
.ps_landing {
  background-color: var(--darkGreen);
  height: 100vh;
  padding: 8vh 10vh;
  box-sizing: border-box;
  z-index: 18;
}
.ps_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 5;
  position: fixed;
  width:100%;
  background-color: var(--darkGreen);
  top:0px;
  left:0px;
  padding:2rem 3rem;
  padding-bottom: 1rem;
  z-index:40000;
}

.ps_logo {
  color: white;
  font-size: calc(var(--titleSize) * 2);
}
.ps_header a,
.ps_header a:visited {
  color: white;
  font-size: var(--normalSize);
  margin-left: 5vw;
  transition: 200ms;
  cursor: pointer;
}
.ps_header a:hover {
  color: var(--orange);
}
.ps_header button {
  margin-left: 10vh;
  color: var(--darkGreen);
  background-color: var(--orange);
  width: 3rem;
  height: 2rem;
  border-top-right-radius: 40%;
  border-bottom-right-radius: 40%;
}
.ps_mainLanding {
  height: 100%;
  display: flex;
  font-family: "Times New Roman", Times, serif;
  letter-spacing: 2px;
}
.ps_mainLanding > div {
  min-width: 50%;
  position: relative;
}

.ps_mainLandingPic img {
  width: 50vh;
  height: 70vh;
  border-top-left-radius: 200px;
  border-top-right-radius: 200px;
  z-index: 1;
  margin-top:20%;
}
.ps_mainLandingPic {
  width: 60vh;
  height: 80vh;
  position: relative;
  z-index: 1;
  margin-left: 35%;
  margin-top: -5%;
}

.ps_mainLandingPic::after {
  content: "";
  position: absolute;
  border: 3px solid grey;
  width:100%;
  height: 90%;
  border-top-left-radius: 200px;
  border-top-right-radius: 200px;
  top: 20%;
  left: 10%;
  z-index: -1;
}
.ps_mainTitle {
  color: white;
  font-size: calc(var(--titleSize) * 7);
  position: absolute;
  top: -5vh;
  left: -10vw;
  z-index: 3;
  letter-spacing: 10px;
}
.ps_mainTitle span {
  font-size: calc(var(--titleSize) * 20);
}
.ps_mainLanding > div:last-of-type p:last-of-type {
  color: var(--orange);
  font-size: var(--titleSize);
  width: 60%;
  margin-top: 60%;
  margin-left: 10%;
  line-height: 150%;
  z-index: 50;
  position: relative;
}
.ps_info {
  padding: 5vh 10vh;
  padding-right: 20vh;
}
.ps_infoSubtitle {
  font-size: var(--subTitleSize);
  color: var(--darkGreen);
}
.ps_infoContent {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.ps_infoContent p {
  color: var(--darkGreen);
  width: 25%;
  margin-left: 17%;
  font-size: calc(var(--normalSize) + 1px);
  line-height: 150%;
}
.ps_infoContent div {
  width: 30vh;
  height: 50vh;
  position: relative;
  z-index: 1;
  margin-top: -5%;
  margin-right: 10%;
}
.ps_infoContent img {
  border-top-left-radius: 200px;
  border-top-right-radius: 200px;
  width: 100%;
  height: 100%;
}
.ps_infoContent div::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  border-top-left-radius: 200px;
  border-top-right-radius: 200px;
  border: 3px solid var(--darkGreen);
  top: 5%;
  left: 15%;
  z-index: -1;
}
.ps_defaultLimit {
  box-sizing: border-box;
  height: 2.5rem;
  background-color: var(--darkGreen);
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
  color: white;
  font-size: calc(var(--normalSize) + 1px);
  padding: 0vh 5vh;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.ps_productArea {
  margin: 20vh 12vh;
  box-sizing: border-box;
}
.ps_products {
  margin-top: 10%;
  display: grid;
  grid-template-columns: auto auto;
  column-gap: 20%;
  row-gap: 8%;
  padding: 6%;
}
.ps_product {
  width: 80%;
  height: 30%;
  border-top-left-radius: 200px;
  border-top-right-radius: 200px;
  position: relative;
}
.ps_product img {
  width: 100%;
  height: 25rem;
  border-top-left-radius: 200px;
  border-top-right-radius: 200px;
  margin-left:4px;
  margin-top:4px;
}
.ps_product:before {
  content: "";
  width: 100%;
  height: 34rem;
  position: absolute;
  border: 4px solid var(--darkGreen);
  border-top-left-radius: 200px;
  border-top-right-radius: 200px;
  z-index: -1;
}

.ps_product p {
  color: var(--darkGreen);
  font-size: var(--titleSize);
  font-weight: bold;
  text-align: center;
  position: relative;
}
.ps_product p span {
  color: var(--orange);
  font-size: calc(var(--titleSize) * 3);
  position: absolute;
  top: -4rem;
  margin-left: 15%;
}
.ps_productTag {
  border: 2px solid var(--lightGreen);
  color: var(--orange);
  border-radius: 20px;
  width: fit-content;
  padding: 1vh 3vh;
  margin: 0.5rem;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
}
.ps_productTags {
  display: flex;
  justify-content: space-evenly;
  margin-top: 4%;
  padding:0px 2rem;
}
.ps_buttonWrapper{
  background-color: rgba(0, 0, 0, 0.23);
  backdrop-filter: blur(5px);
  transition:600ms;
  position: absolute;
  top:2px;
  left:4px;
  border-top-left-radius: 200px;
  border-top-right-radius: 200px;
  height:25rem;
  width:100%;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-transition:600ms;
  -moz-transition:600ms;
  -ms-transition:600ms;
  -o-transition:600ms;
}
.ps_buttonWrapper button {
  color: white;
  background-color: var(--lightGreen);
  border-radius: 40px;
  font-size: var(--titleSize);
  padding: 2vh 10vh;
  margin-top: 1%;
  transition: 200ms;
  -webkit-transition: 200ms;
  -moz-transition: 200ms;
  -ms-transition: 200ms;
  -o-transition: 200ms;
  -webkit-border-radius: 40px;
  -moz-border-radius: 40px;
  -ms-border-radius: 40px;
  -o-border-radius: 40px;
}
.ps_buttonWrapper button:hover {
  background-color: var(--orange);
}
.ps_cart {
  position: fixed;
  top: 0px;
  left: 50vw;
  width: 50vw;
  height: 100vh;
  background-color: white;
  z-index: 200;
  backdrop-filter: blur(10px);
  background-color: #1e2920d6;
  box-sizing: border-box;
  padding: 5vh;
  z-index: 400000;
}
.ps_wrapper {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}
.ps_mainWrapper {
  position: relative;
}
.ps_cartTitle {
  color: white;
  font-size: calc(var(--titleSize) + 2px);
  padding-right:4rem;
}
.ps_cartEmpty {
  font-size: var(--titleSize);
  color: white;
  margin-left: 10%;
  margin-top: 10%;
}
.ps_cartFooter {
  position: absolute;
  bottom: 5vh;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
}
.ps_cartFooter button {
  background-color: var(--orange);
  color: white;
  border: 1px solid var(--darkGreen);
  padding: 1.5vh 6vh;
  font-size: var(--titleSize);
  border-radius: 25px;
}
.ps_cartProduct > span div:nth-of-type(1) {
  font-size: calc(var(--titleSize) + 3px);
  margin-right: 2vh;
}
.ps_cartProduct {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  margin-left: 10%;
  margin-top: 5%;
  color: white;
}
.ps_cartProduct span {
  display: flex;
  align-items: center;
}
.ps_cartProduct input {
  font-size: var(--titleSize);
  width: 5vw;
  outline: none;
}
.ps_cartProduct button {
  border: none;
  background-color: transparent;
  color: white;
  font-size: var(--titleSize);
  margin-left: 2vw;
}
.ps_cartClose {
  float: right;
  border: none;
  padding-right: 6vh;
  background-color: transparent;
  font-size: calc(var(--titleSize) + 20px);
  color: var(--orange);
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -o-transform: rotate(45deg);
}
.ps_cartClose:hover {
  color: var(--lightGreen);
}
.ps_cartProdWrapper{
  overflow-y:auto;
  max-height:70vh;
   scrollbar-gutter: stable;
   padding-bottom:3rem;
}

.ps_alert {
  position: fixed;
  top: 15%;
  left: 68vw;
  height: 20vh;
  width: 30vw;
  z-index: 200;
  backdrop-filter: blur(100px);
  background-color: white;
  box-sizing: border-box;
  padding: 5vh;
  color: var(--orange);
  border-radius: 17px;
  font-size: 1.2em;
  border: 10px solid var(--lightGreen);
}
.ps_cartProduct img {
  width: 4rem;
  height: 4rem;
  margin-right: 3rem;
}

.content-enter {
  width: 0;
  overflow: hidden;
}

.content-enter-active {
  width: 200px; /* Adjust width according to your requirement */
  transition: width 0.3s ease-in-out;
}

.content-exit {
  width: 200px; /* Adjust width according to your requirement */
}

.content-exit-active {
  width: 0;
  transition: width 0.3s ease-in-out;
}

.ps_myVase {
  padding: 5vh 10vh;
  padding-right: 20vh;
  width: 100%;
  padding-top: 20%;

}
#ps_myVase{
  scroll-margin-top:1rem;
}
.ps_myVase .ps_infoSubtitle {
  margin-top: 1vh;
}
.ps_myVaseText {
  color: var(--darkGreen);
  font-size: 1.3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 15vh auto;
  margin-top: 5vh;
}
.ps_myVaseText p {
  text-align: center;
  width: 30%;
  margin-right: 20vh;
}
.ps_myVaseImage {
  height: 25rem;
  width: 15rem;
  border: 6px solid var(--darkGreen);
  border-top-left-radius: 200px;
  border-top-right-radius: 200px;
  margin-left: 30vh;
  position: relative;
  box-sizing: border-box;
  z-index: 550;
}
.ps_myVaseImage::after {
  content: "";
  position: absolute;
  top: 5vh;
  left: 5vh;
  height: 25rem;
  width: 15rem;
  border: 2px solid var(--lightGreen);
  z-index: 100;
  border-top-left-radius: 200px;
  border-top-right-radius: 200px;
}
.ps_myVaseContact {
  display: flex;
}
.ps_myVaseContact > div {
  width: 50%;
  font-size: var(--titleSize);
  color: var(--darkGreen);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  padding: 10vh;
  font-weight: bold;
  position: relative;
}
.ps_myVaseContact > div:last-of-type {
  align-items: flex-start;
  /* margin-left: 10vh; */
}
.ps_myVaseContact > div span {
  font-weight: normal;
}
.ps_myVaseCopy {
  font-size: calc(var(--titleSize) - 5px);
  color: grey;
  margin-left: 1rem;
  cursor: pointer;
}
.ps_myVaseCopyActive {
  color: var(--orange);
}
.tooltip {
  border: 1px solid var(--lightGreen);
  color: var(--lightGreen);
  width: min-content;
  height: min-content;
  font-size: calc(var(--normalSize) - 5px);
  padding: 0.5rem;
  border-radius: 5px;
  right: -5%;
  top: 2%;
  position: relative;
}
.ps_footer {
  background-color: var(--darkGreen);
  width: 100%;
  height: 50vh;
  padding: 10vh;
  padding-bottom: 50vh;
  padding-right: 20vh;
  padding-top: 5%;
  display: flex;
  justify-content: space-between;
}
.ps_footer div {
  width: 60%;
  color: var(--orange);
  font-size: calc(var(--titleSize) - 2px);
  line-height: 200%;
}
.ps_footer span p {
  color: var(--lightGreen);
}
.ps_footer span p:hover {
  color: var(--orange);
  cursor: pointer;
}
.ps_footer span {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

